











































































































import Home from "./components/home/home"
import Sprites from "./components/gallery/sprites"
import Editor from "./components/editor/editor"

export default

	components:
		"home": Home
		"sprites": Sprites
		"editor": Editor

	data: () ->
		sprites: []
		sideBarDisplay: "block"
		currentTab: "home"
		currentSprite: null

	methods:
		selectTab: (tab) ->
			this.currentTab = tab

		editSprite: (sprite) ->
			this.currentSprite = sprite
			this.currentTab = "editor"

		fetchSprites: () ->
			url = "http://localhost:8000/api/sprites/"
			options=
				headers:
					"accept": "application/json"
			self = this
			fetch(url)
			.catch((error) ->
				console.log error)
			.then((response) ->
				console.log response
				response.json()
				)
			.then((json) ->
				console.log json
				self.sprites = json
				)

	mounted: () ->
		this.fetchSprites()


