
















































export default
	props:
		display:
			type: Boolean
			required: true

	data: () ->
		showNewSprite: false
		selectedFile: null
		spriteName: ""
		processing: false

	computed:
		modalDisplay: () ->
			if this.display then "block" else "none"

	methods:
		selectFile: (event) ->
			this.selectedFile = event.target.files[0]
			re = /(\w+)\.\w+$/
			if re.test(this.selectedFile.name)
				this.spriteName = re.exec(this.selectedFile.name)[1]
			else
				this.spriteName = this.selectedFile.name
			console.log this.spriteName

		uploadNewSprite: () ->
			this.processing = true
			url = "http://localhost:8000/api/sprites/"
			formData = new FormData()
			formData.append("name", this.spriteName)
			formData.append("sprite", this.selectedFile)

			options =
				method: "POST"
				body:
					formData

			self = this
			fetch(url, options)
			.catch((error) ->
				console.log(error)
				)
			.then((response) ->
				response.json()
				)
			.then((json) ->
				self.processing = false
				console.log(json)
				self.processSprite(json.id)
				self.$emit('update:display', false)
				)

		processSprite: (id) ->
			url = "http://localhost:8000/api/sprites/#{id}/process/"
			options =
				method: "PUT"

			self = this

			fetch(url, options)
			.catch((error) ->
				console.log(error)
				)
			.then((response) ->
				response.json()
				)
			.then((json) ->
				console.log(json)
				self.$emit("new-sprite", json)
				)
